header.header-mobile{
	position: fixed;
	z-index: 99;

	.header-mobile-container{
		height: 60px;
	}//header-mobile-container


	.mobile-logo-image{
		width: 73px;
		margin: 3px 0;
	}//mobile-logo-image

}//header-mobile


#mobile, 
.mobile-menu{
	background: #0C151E !important;
	margin-top: 60px;
}//mobile-menu


#mobile>div.menu li a, 
#mobile>div.menu__container li a, 
#mobile>ul.menu li a, 
#mobile>ul.menu__container li a, 
.mobile-menu>div.menu li a, 
.mobile-menu>div.menu__container li a, 
.mobile-menu>ul.menu li a, 
.mobile-menu>ul.menu__container li a{
	color: #fff !important;

	&:hover,&:active,&:focus{
		color: #fff !important;
	}
}



header.header-mobile .header-mobile-container .row a{
	vertical-align: top;
}


@import 'responsive-lg';
@import 'responsive-md';
@import 'responsive-sm';
@import 'responsive-xs';